<template>
        <el-collapse v-model="activeNames">
                <template v-for="(li, idx) in PWA_LIST" :key="idx">
                        <el-collapse-item :title="li.title" :name="idx" v-if="li.steps?.length > 0">
                                <ul>
                                        <li v-for="(child, index) in li.steps" class="collapseItem">
                                                <p class="text">
                                                        {{ index + 1 }} . {{ t(child.txt) }}
                                                </p>
                                                <img class="icon" alt="" :src="child.img" />
                                        </li>
                                </ul>
                        </el-collapse-item>
                </template>
        </el-collapse>
</template>
<script setup>
import { PWA_LIST } from '@/data/homeScreen'
import { ref } from 'vue'
const activeNames = ref([])
const { t } = useI18n()
onMounted(()=>{
        const mwRoute = useRoute();
        activeNames.value = mwRoute.query.key==1 ? [0] : [1,2]
})
</script>
<style scoped lang="scss">
.el-collapse {
        border-top: none;
        border-bottom: none;
}
:deep(.el-collapse-item) {
        margin-bottom: 5.3333vw;
}
:deep(.el-collapse-item__header) {
        background-color: transparent;
        @include setfont(4.8vw, #ffffff, 600);
        height: 6.4vw;
        border-bottom: none;
        transition: none;
        &.is-active {
                border-bottom: 1px solid transparent;
        }
}
:deep(.el-collapse-item__wrap) {
        margin-top: 4.2667vw;
        background-color: transparent;
        border-bottom: none;
        .el-collapse-item__content {
                padding-bottom: 0;
        }
}
.collapseItem {
        box-sizing: border-box;
        width: 100%;
        height: auto;
        margin-bottom: 3.2vw;
        display: flex;
        flex-direction: column;
        &:last-child {
                margin-bottom: 0;
        }
        .icon {
                width: 100%;
                height: auto;
                border-radius: 5.3333vw;
                margin-bottom: 5.3333vw;
        }
        .text {
                @include setfont(3.7333vw, #d6d1d1);
                line-height: 1.2;
                margin-bottom: 2.6667vw;
        }
}
</style>
